export const getStep = async (
  stepKey: string
): Promise<GoogleSpreadsheetRow | null> => {
  const doc = await loadOrGet();
  const team = window.localStorage.getItem("team");
  if (team === null) {
    return null;
  }

  const sheet = doc!.sheetsByTitle[team]; // or use doc.sheetsById[id] or doc.sheetsByTitle[title]
  const rows = await sheet.getRows(); // can pass in { limit, offset }
  let findRow: GoogleSpreadsheetRow | null = null;
  for (const row of rows) {
    if (row.step_id === stepKey) {
      findRow = row;
      break;
    }
  }
  if (findRow == null) {
    throw new Error(`Row not found for key: ${stepKey}`);
  }

  return findRow!;
};

export const loadTeams = async (): Promise<string[]> => {
  const doc = await loadOrGet();
  const sheets = doc!.sheetsByTitle;
  const teams = Object.keys(sheets).filter((title) => title.startsWith("Team"));
  return teams.sort();
};

export const loadRules = async (): Promise<string[]> => {
  const doc = await loadOrGet();
  const rules = doc!.sheetsByTitle["Rules"];
  const rows = await rules.getRows();
  return rows.map((r) => r.rules as string);
};

export const getImage = async (imageIndex: number): Promise<string | null> => {
  const doc = await loadOrGet();
  const sheet = doc!.sheetsByTitle["Images"]; // or use doc.sheetsById[id] or doc.sheetsByTitle[title]

  await sheet.loadCells({
    startRowIndex: imageIndex - 1,
    endRowIndex: imageIndex,
    startColumnIndex: 4,
    endColumnIndex: 128,
  });

  let dataString = "";
  for (let i = 0; i < 128; ++i) {
    const dataCell = sheet.getCell(imageIndex - 1, i + 4);
    if (!dataCell || dataCell.value === "" || dataCell.value == null) {
      break;
    }
    dataString += dataCell.value;
  }

  return dataString;
};

export const fullyWriteStepComplete = async (
  stepIndex: number,
  navigator: Ons.Navigator,
  setLoading: (status: boolean) => unknown
) => {
  const doc = await loadOrGet();
  const team = window.localStorage.getItem("team");
  if (team === null) {
    setLoading(false);

    return null;
  }

  const sheet = doc!.sheetsByTitle[team]; // or use doc.sheetsById[id] or doc.sheetsByTitle[title]
  const rows = await sheet.getRows({ limit: 1, offset: stepIndex - 2 });
  if (!rows) {
    setLoading(false);
    return;
  }
  console.log("ROW I HAVE IS", rows[0]!.step_id);
  rows[0].step_status = "DONE";
  await rows[0].save();
  navigator.popPage({ data: { refresh: true, navigator } });
};
